
import React from 'react'

export default function MealInfo({id, title, description , type, country, time, recipeURL, imageURL}) {
  return (
    <>
    <div className='pt-36'>
    <div className='text-center mx-auto p-3 m-3 rounded-full bg-orange-700/50 w-48 text-white font-extralight text-lg'>Meal # {id}</div>
      <div className='mx-auto  w-2/5 h-2/5 grid grid-cols-2'>
      <div className=' col-span-1 border-white border rounded-3xl mx-3 '><img className='object-cover h-full rounded-3xl'
       src={imageURL} alt={title}  /></div>
      <div className='grid col-span-1 p-3 rounded-2xl bg-orange-50/90'>
        <h1 className='text-center text-3xl text-orange-800 font-light'>{title}</h1>


<div className='py-1  text-justify text-sm'> {description} 
<hr className='border-black border-dotted'></hr>
</div>


<div className='flex justify-between'><p className='font-semibold'>Type of Dish</p> <p className='font-medium text-orange-700'>{type}</p></div>

<div className='flex justify-between'><p className='font-semibold'>Cooking Time</p> <p className='font-medium text-orange-700'>{time} min</p></div>

<div className='flex justify-between'><p className='font-semibold'>Country Origin</p> <p className='font-medium text-orange-700'>{country}</p></div>

<div className='grid content-end py-3'>
<hr className='border-black border-dotted'></hr>
<p className='text-sm text-center'><a rel="noreferrer" className='text-orange-700/60 hover:text-orange-800' target="_blank" href={recipeURL}>Recipe Source</a> | <a rel="noreferrer"  target="_blank" href={imageURL}  className='text-orange-700/60 hover:text-orange-800'>Image Source</a> </p>
</div>
      </div>
      </div>

      </div>
    </>
  )
}

