import React, { useState, useEffect } from 'react';

import MealDirectory from './components/MealDirectory';
import axios from "axios"




function App() {
   

  const [meals, setMeals] = useState([]);

  useEffect(() => {
    axios.get("/api/meals/")
    .then(response => { setMeals(response.data);

    })
    .catch(error => {console.error(error);});
  }, []);


  return (
    <div className='bg-gradient-to-b from-yellow-50  to bg-orange-200 h-svh grid content-center'>
      <div className=''>
      <div className='text-center p-10'><h1 className='text-4xl font-light rounded-t-2xl bg-orange-700/60 w-1/3 mx-auto text-white p-3'>My Favorite Meals</h1>
      <div className='p-5 mb-5 text-justify shadow-md border border-orange-800/30 rounded-b-2xl bg-white/40 w-1/3 mx-auto'>For this interview assignment, I decided to make a webpage about my own favorite foods inspired by  the sentiment behind KangaCook. Using  Django REST framework, I have created an API to post, store, and retrieve the data of each meal.</div>
     <MealDirectory
     meals={meals}
     />
    </div>

<div className='text-xs text-center text-orange-700'>Jasmine Kingg  2024</div>
    </div>
    </div>

    
  );
}

export default App;