import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import MealInfo from './MealInfo';

function MealModal({id, title, description , type, country, time, recipeURL, imageURL}) {
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
  
    return (
      <div >
        <button 
        onClick={handleOpen}
         className='w-36 rounded-full mx-1 transition ease-linear  text-orange-800 hover:bg-orange-700/80 hover:text-white bg-orange-100/90 p-3'
        >Meal {id}</button>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
         
          <Box >
          <div
            className='my-auto'>
          <MealInfo
          id={id}
        title={title}
         description={description}
         type={type}
         country={country}
        time={time}
        recipeURL={recipeURL}
        imageURL={imageURL}
          
          /> </div>
          </Box>
         
        </Modal>
      </div>
    );
}
export default MealModal